.wrap {
    position: relative;
    display: flex;
    width: 100%;
    // height: 100vh;
    height: calc(var(--vhd, 1vh) * 100);
    background: var(--greeny);
    color: var(--white);
    overflow: hidden;
}

.inner {
    padding-top: 2.5rem;
    display: flex;
    flex-direction: column;
    // height: calc(100vh - 1.56rem);

    height: calc(var(--vhd, 1vh) * 100 - 2.31rem);
    width: 100%;
    // overflow: hidden;
    p{
        width: 22.3rem;
    }
}

.form {
    margin-top: auto;
	&__title{
		font-size: 1.38rem;
		line-height: 1.56rem;
	}
    &__body {
		
		margin-top: 1rem;
        position: relative;
        display: flex;
        flex-direction: column;
		border-radius: 0.75rem;
        height: 9.38rem;
		overflow: hidden;
    }

    &__label {
        padding: 0.75rem 0.75rem 1.25rem;

		height: 5.63rem;
        display: flex;
        flex-direction: column;
		border-radius: 0.75rem;
		background: rgba(255, 255, 255, 0.15)
    }
	&__desc{
		font-size: 0.75rem;
		font-weight: 400;
		letter-spacing: 0.07em;
		text-transform: uppercase;
	}
    &__input {
		margin-top: 0.5rem;
        padding: 0.31rem 0;
        height: 1.88rem;
        border: none;
        background: none;
        border-bottom: 0.06rem solid var(--white);
        outline: unset;
        font-weight: 300;
        user-select: unset;
        &_disable {

            border-bottom: 0.06rem solid rgba(255, 255, 255, 0.2);
        }
    }

    &__btn {
		margin-top: 0.63rem;
        width: 100%;
        height: 3.13rem;
        display: flex;
        align-items: center;
        justify-content: center;
        outline: unset;
        border-radius: 0.75rem;
        border: 0.06rem solid var(--white);
        color: var(--black);
        background: var(--white);
        letter-spacing: 0.07em;
        text-transform: uppercase;
        font-weight: 400;
        font-size: 0.75rem;
        transition: all ease 0.3s;

        &_disable {
            background: transparent;
            color: var(--white);
            opacity: 0.2;
            pointer-events: none;
        }
        &_again {
			margin-top: auto;
            background: transparent;
            color: var(--black);
			border: none;
        }
        &_again_disable {
            opacity: 0.2;
			pointer-events: none;
        }
		
    }
	&__panel{
		display: flex;
		flex-direction: column;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-radius: 0.75rem;
		background: #F7F9A5;
		color: var(--black);
		z-index: 1000;
		
	}
	&__text{
        padding: 0.75rem 0.75rem 0;
		span{
			font-weight: 500;
		}
	}

}

.case{
    margin-top: auto;
    padding: 0.5rem 0.55rem 1.25rem;
    height: 15.63rem;
    width: 100%;
    border-radius: 0.75rem;
    display: flex;
    flex-direction: column;
    border: 0.06rem solid rgba(255, 255, 255, 0.3);
    &_access{
        background: rgba(255, 255, 255, 0.15);
        border-color: transparent;
    }
    &__label{
        display: flex;
        align-items: center;
        margin-left: 0.1rem;
        font-size: 0.75rem;
        line-height: 1.25rem;
        font-weight: 400;
        text-transform: uppercase;
        letter-spacing: 0.07em;
        span{
            margin-left: 0.5rem;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 0 0.75rem;
            height: 1.5rem;
            font-size: 0.75rem;
            line-height: 1.25rem;
            letter-spacing: 0.07em;
            font-weight: 400;
            text-transform: uppercase;
            color: black;
            background: var(--yellow);
            border-radius: 0.50rem;
        }
    }
    &__title{
        margin-top: 0.5rem;
        font-size: 1.75rem;
        line-height: 1.88rem;
    }
    &__btns{
        margin-top: auto;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__btn{
        display: flex;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        outline: none;
        width: 10.19rem;
        height: 3.13rem;
        border-radius: 0.75rem;
        font-size: 0.75rem;
        line-height: 1.25rem;
        text-transform: uppercase;
        letter-spacing: 0.07em;
        background: rgba(255, 255, 255, 0.15);
        &_access{
            background: var(--yellow);
            color: var(--black);
        }
    }
}