:root {
    --white: #FFFFFF;
    --grey: #F8F8F8;
    --yellow: #F7F9A5;
    --greeny: #78A671;
    --purple: #6D9FB2;
    --black: #1A1A1A;
    --green-on-white: rgba(134, 216, 157, 0.2);
}


html {
    font-size: 4.27vw;
    width: 100%;
    height: 100%;
    background: var(--white);
    -webkit-font-smoothing: antialiased;
    overscroll-behavior: none;
    -webkit-overflow-scrolling: auto;
}

body {
    background: var(--white);
    color: var(--black);
    -webkit-font-smoothing: antialiased;
    overscroll-behavior: none;
    -webkit-overflow-scrolling: auto;
    font-family: 'Mabry Pro', sans-serif;
    width: 100%;
    
    // height: 100%;
    font-size: 1rem;
    line-height: 1.25rem;
    font-weight: 300;
    transition-property: opacity, height;
    transition-timing-function: ease, ease;
    transition-duration: 0.3s, 0.5s;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    flex-shrink: 0;
    user-select: none;
}

textarea{
    font: inherit;
}
body, #root, .wrap {
    height: 100%;
}

.wrap {
    position: relative;
    width: 100%;
    z-index: 10;
    
    // -webkit-overflow-scrolling: auto;
        // overflow-y: scroll;
}
.container{
    position: relative;
    width: 100%;
    padding-left: 0.63rem;
    padding-right: 0.63rem;
}
img, svg {
    height: auto;
    width: 100%;
    flex-shrink: 0;
}
h1{
    font-size: 2.50rem;
    line-height: 2.50rem;
    font-weight: 300;
}
span, p, input, button, a {
    font: inherit;
    color: inherit;
}

h2, .body-28{
    font-weight: 300;
    margin-bottom: 0.83rem;
    font-size: 1.75rem;
    line-height: 1.88rem;
}
.card-body-28{
    padding-top: 0.2rem;
    margin-bottom: 0.63rem;
    font-size: 1.75rem;
    line-height: 1.88rem;
}
.body-22{
    font-size: 1.38rem;
    line-height: 1.56rem;
}
.card-body-22{
    padding-top: 0.1rem;
    font-size: 1.38rem;
    line-height: 1.56rem;
    margin-bottom: 0.63rem;
}

p+p{
    margin-top: 0.5rem;
}
.js-fade-in{
    transform: translateY(1rem);
    opacity: 0;
}
.sortable-ghost{
opacity: 0;
}
@media screen and ( min-width: 641px) {
    html{
        font-size: 16px;
        font-size: 2.39vh;
    }
    .container{
        margin: 0 auto;
        position: relative;
        width: 23.44rem;
        padding-left: 0.63rem;
        padding-right: 0.63rem;
    }
}