.wrap{
	position: absolute;
	top: 0;
	left: 0;
    padding-left: 0.63rem;
    padding-right: 0.63rem;
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	height: 1.88rem;
	z-index: 1000000;

	color: var(--white);
	
}

.title{
	font-weight: 300;
	text-decoration: none;
}

@media screen and ( min-width: 641px) {
    .wrap{
        margin: 0 auto;
        width: 23.44rem;
		left: 50%;
		transform: translate(-50%);
    }
}