.wrap {
    position: relative;
    display: flex;
    width: 100%;
    // height: 100vh;
    height: calc(var(--vhd, 1vh) * 100);
    background: var(--greeny);
    color: var(--white);
    overflow: hidden;
}

.inner {
    padding-top: 2.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: calc(var(--vhd, 1vh) * 100 - 2.31rem);
    width: 100%;
    
}
.label{
    padding: 2.5rem 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    border-radius: 0.75rem;
    background: #000;
    text-align: center;
    background: rgba(255, 255, 255, 0.15);
}